@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__body {
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: normal;
  color: #23272a;
  display: grid;
  min-height: 100%;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  font-size: 15px;
  line-height: 1.6;
}
.page__body::after {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.35);
}

.container {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 110px;
}

@media (max-width: 280px) {
  .container {
    padding: 0 20px;
  }
}
@media (max-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
.page-header {
  background: #4965ee;
}

.page-header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.page-header__list {
  list-style-type: none;
  display: flex;
  margin: 0 -18px;
}

.page-header__item {
  margin: 0 18px;
  transition: 0.3s border-bottom ease-in;
}
.page-header__item:hover {
  border-bottom: 2px solid #ffffff;
}

.page-header__link {
  text-decoration: none;
  color: #ffffff;
  opacity: 0.6;
  transition: 0.3s background ease-in;
  padding: 31px 14px;
  display: block;
  font-weight: 500;
}
.page-header__link:hover {
  opacity: 1;
  padding: 30px 14px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 0 0 4px 4px;
}

.page-header__user {
  display: flex;
  align-items: center;
}

.page-header__entry {
  color: #ffffff;
  text-decoration: none;
  font-weight: 500;
  padding: 10px 24px 11px 51px;
  background: #23272a no-repeat;
  border-radius: 4px;
  background-image: url("../img/login.svg");
  background-position: 20px center;
  transition: 0.3s background-color ease-in;
}
.page-header__entry:hover {
  background-color: #171a1c;
}

.page-header__button-theme {
  margin-right: 46px;
  background: none;
  border: none;
  fill: #ffffff;
  transition: 0.3s opacity ease-in;
}
.page-header__button-theme:hover {
  opacity: 0.8;
}

.page-footer {
  background: #25292c;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}

.page-footer__container {
  color: #ffffff;
  padding: 38px 0 28px 0;
  display: grid;
}

.page-footer__wrapper {
  display: flex;
  gap: 140px;
  margin-bottom: 70px;
}

.page-footer__copyright {
  font-size: 13px;
  line-height: 19px;
  margin: 0 auto;
  opacity: 0.15;
}

.page-footer__heading {
  color: #4965ee;
  font-weight: 500;
  margin-bottom: 10px;
}

.page-footer__list {
  list-style-type: none;
}

.page-footer__link {
  color: #ffffff;
  text-decoration: none;
  margin-bottom: 15px;
  padding: 5px 0;
}

.page-footer__link-mail {
  text-decoration: none;
  color: #4965ee;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #ffffff;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.35);
  border-radius: 5px;
  z-index: 100;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s opacity ease-in;
}
.modal--show {
  opacity: 1;
  visibility: visible;
}
.modal-blackout::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.35);
  transition: 0.3s background ease-in;
}

.modal-entrance__container {
  display: flex;
  align-items: center;
  height: 100%;
}

.modal-entrance__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
  padding: 0 48px;
}

.modal-entrance__heading {
  font-weight: bold;
  font-size: 25px;
  line-height: 55px;
  margin-bottom: 17px;
}

.modal-entrance__link-vk {
  font-weight: bold;
  font-size: 15px;
  line-height: 24px;
  padding: 17px 12px 17px 86px;
  border: 2px solid #23272a;
  border-radius: 5px;
  position: relative;
  margin-bottom: 17px;
  transition: 0.3s opacity ease-in;
}
.modal-entrance__link-vk::before {
  content: "";
  position: absolute;
  width: 36px;
  height: 36px;
  top: 50%;
  transform: translateY(-50%);
  left: 33px;
  background: url("../img/popup-entrance/vk.png");
}
.modal-entrance__link-vk:hover {
  opacity: 0.6;
}

.modal-entrance__or {
  font-size: 15px;
  line-height: 24px;
  color: rgba(35, 39, 42, 0.4);
  margin-bottom: 17px;
}

.modal-entrance__illustration {
  background: #4965ee;
  border-radius: 5px 0 0 5px;
  height: 100%;
  padding: 75px 17px 74px;
}

.modal-entrance__list {
  list-style-type: none;
  display: grid;
  gap: 17px;
}

.modal-entrance__input {
  font-size: 17px;
  line-height: 24px;
  color: rgba(35, 39, 42, 0.4);
  border-radius: 5px;
  background: rgba(73, 101, 238, 0.15);
  border: none;
  padding: 15px 0 15px 33px;
  width: 100%;
}

.modal-entrance__form {
  width: 100%;
  margin-bottom: 17px;
}

.modal-entrance__item {
  position: relative;
}
.modal-entrance__item::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 9px;
  background-repeat: no-repeat;
  transform: translateY(-50%);
}
.modal-entrance__item--email::before {
  background-image: url("../img/icons/icon-mail.svg");
  width: 14px;
  height: 11px;
}
.modal-entrance__item--password::before {
  background-image: url("../img/icons/icon-password.svg");
  width: 13.5px;
  height: 13.5px;
}

.modal-entrance__button {
  width: 100%;
  background: #4965ee;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  padding: 15px 0;
  cursor: pointer;
  transition: 0.3s background ease-in;
}
.modal-entrance__button:hover {
  background: #6079f0;
}

.modal-entrance__link {
  font-size: 15px;
  line-height: 24px;
  color: #4965ee;
  text-decoration: none;
}
.modal-entrance__link--forgot {
  margin-bottom: 17px;
}

.modal-entrance__no-account {
  font-size: 15px;
  line-height: 24px;
}

.modal-entrance__close {
  position: absolute;
  border: none;
  background: none;
  top: 15px;
  right: 15px;
  cursor: pointer;
}
.modal-entrance__close svg {
  width: 20px;
  height: 20px;
}

.promo {
  background: url("../img/promo-background.jpg") no-repeat;
  background-size: cover;
  color: #ffffff;
}

.promo__container {
  padding: 123px 0 278px 0;
}

.promo__heading {
  font-weight: 900;
  font-size: 35px;
  line-height: 52px;
  margin-bottom: 24px;
}

.promo__paragraph {
  width: 33%;
  margin-bottom: 54px;
}

.promo__buttons {
  display: flex;
}

.promo__button {
  text-decoration: none;
  font-weight: 500;
  font-size: 17px;
  line-height: 25px;
  padding: 12px 23px;
  border-radius: 4px;
}
.promo__button--add {
  background: #ffffff;
  color: #476cea;
  margin-right: 21px;
  transition: 0.3s opacity ease-in;
}
.promo__button--add:hover {
  opacity: 0.7;
}
.promo__button--tune {
  background: #23272a;
  color: #ffffff;
  transition: 0.3s background ease-in;
}
.promo__button--tune:hover {
  background: #171a1c;
}

.about__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 83%;
  margin: 0 auto;
}

.about__heading {
  font-weight: 900;
  font-size: 35px;
  line-height: 55px;
  color: #23272a;
  margin-bottom: 16px;
}

.about__content {
  width: 41%;
}

.about__paragraph {
  width: 92%;
}

.about__container {
  display: grid;
  gap: 128px;
  padding: 94px 0;
}

.statistics {
  background: url("../img/index/about/background.png") no-repeat;
  background-size: cover;
  color: #ffffff;
}

.statistics__container {
  padding: 225px 0 323px 0;
}

.statistics__heading {
  font-weight: 900;
  font-size: 35px;
  line-height: 55px;
  text-align: center;
  margin-bottom: 15px;
}

.statistics__paragraph {
  font-size: 15px;
  line-height: 24px;
  text-align: center;
  width: 54%;
  margin: 0 auto;
  margin-bottom: 70px;
}

.statistics__list {
  display: flex;
  justify-content: center;
  gap: 39px;
  list-style-type: none;
}

.statistics__item {
  display: flex;
}

.statistics__content {
  margin-left: 15px;
  font-weight: bold;
}

.statistics__number {
  font-size: 50px;
  line-height: 50px;
}

.statistics__description {
  font-size: 16px;
  line-height: 24px;
}

.subscribe {
  position: relative;
  background: #4965ee;
  color: #ffffff;
}
.subscribe::after {
  content: "";
  width: 100%;
  height: 233px;
  position: absolute;
  background: url("../img/index/subscribe/subscribe-decoration.svg") no-repeat;
  background-size: 100% auto;
  top: -232px;
  left: 0;
}

.subscribe__container {
  display: flex;
  justify-content: space-between;
  padding: 85px 0 38px 0;
  position: relative;
}

.subscribe__heading {
  font-weight: 900;
  font-size: 35px;
  line-height: 55px;
  margin-bottom: 39px;
}

.subscribe__buttons {
  display: flex;
  margin-bottom: 46px;
}

.subscribe__button {
  padding: 13px 17px;
  text-decoration: none;
  font-weight: 500;
  font-size: 17px;
  border-radius: 4px;
  line-height: 25px;
}
.subscribe__button--plug {
  color: #ffffff;
  background: #23272a;
  transition: 0.3s background ease-in;
  margin-right: 38px;
}
.subscribe__button--plug:hover {
  background: #171a1c;
}
.subscribe__button--subscribe {
  color: #4965ee;
  background: #ffffff;
  transition: 0.3s opacity ease-in;
}
.subscribe__button--subscribe:hover {
  opacity: 0.8;
}

.subscribe__persons {
  margin-left: 217px;
}

.subscribe__person + .subscribe__person {
  margin-left: -25px;
}

.subscribe__illustration {
  position: absolute;
  top: -71px;
  z-index: 2;
  right: 0;
}

.registration {
  position: relative;
}
.registration::after {
  content: "";
  width: 48%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  background: rgba(73, 101, 238, 0.15);
}

.registration__heading {
  font-weight: 900;
  font-size: 35px;
  line-height: 55px;
  margin-bottom: 12px;
}

.registration__container {
  display: flex;
  position: relative;
}

.registration__illustration {
  left: -24px;
  top: 106px;
  position: absolute;
  z-index: 2;
}

.registration__wrapper {
  margin-left: auto;
  width: 41%;
  padding: 108px 0 218px 0;
}

.registration__paragraph {
  margin-bottom: 38px;
}

.registration__list {
  position: relative;
  list-style-type: none;
  display: grid;
  gap: 17px;
  width: 100%;
  margin-bottom: 17px;
}

.registration__input {
  position: relative;
  font-size: 17px;
  line-height: 24px;
  color: rgba(35, 39, 42, 0.4);
  border-radius: 5px;
  background: rgba(73, 101, 238, 0.15);
  border: none;
  padding: 15px 0 15px 52px;
  width: 100%;
}
.registration__input:focus {
  outline: none;
  border: 2px solid #4965ee;
  padding: 13px 0 13px 49px;
  color: rgba(35, 39, 42, 0.7);
}
.registration__input:disabled {
  color: #ff4e4e;
}

.registration__item {
  position: relative;
}
.registration__item::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 28px;
  background-repeat: no-repeat;
  transform: translateY(-50%);
}
.registration__item--login::before {
  background-image: url("../img/icons/icon-login.svg");
  width: 14px;
  height: 14px;
}
.registration__item--email::before {
  background-image: url("../img/icons/icon-mail.svg");
  width: 14px;
  height: 11px;
}
.registration__item--vk::before {
  background-image: url("../img/icons/icon-vk.svg");
  width: 16px;
  height: 16px;
}
.registration__item--password::before {
  background-image: url("../img/icons/icon-password.svg");
  width: 13.5px;
  height: 13.5px;
}

.registration__button {
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  color: #ffffff;
  padding: 15px 60px;
  background: #4965ee;
  border-radius: 5px;
  border: none;
  margin-bottom: 27px;
  cursor: pointer;
  transition: 0.3s background ease-in;
}
.registration__button:hover {
  background: #6079f0;
}

.registration__cookie {
  opacity: 0.6;
}

.services__container {
  position: relative;
  padding: 117px 0 130px 0;
}

.services__heading {
  font-weight: 900;
  font-size: 35px;
  line-height: 55px;
  margin-bottom: 16px;
}

.services__paragraph {
  font-size: 15px;
  line-height: 24px;
  width: 44%;
}
.services__paragraph--commands {
  margin-bottom: 24px;
}
.services__paragraph--donate {
  margin-bottom: 105px;
}

.services__illustration {
  position: absolute;
  top: 73px;
  right: 128px;
}

.services__list {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 60px;
}

.services__item {
  border-radius: 10px;
  padding: 20px 10px 20px 20px;
}
.services__item--commands {
  background: rgba(73, 101, 238, 0.15);
}
.services__item--donate {
  background: rgba(238, 172, 73, 0.15);
}

.services__name {
  font-weight: 900;
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 10px;
}

.services__description {
  font-size: 15px;
  line-height: 24px;
  margin-bottom: 19px;
}

.services__button {
  font-weight: 500;
  font-size: 17px;
  line-height: 25px;
  border-radius: 4px;
  padding: 13px 40px;
  color: #ffffff;
  text-decoration: none;
  display: inline-block;
  transition: 0.3s background ease-in;
}
.services__button--commands {
  background: #4965ee;
}
.services__button--commands:hover {
  background: #6079f0;
}
.services__button--donate {
  background: #eeac49;
}
.services__button--donate:hover {
  background: #f0b760;
}

.commands__container {
  padding: 117px 0 146px 0;
  position: relative;
}

.commands__heading {
  font-weight: 900;
  font-size: 35px;
  line-height: 55px;
  margin-bottom: 16px;
}

.commands__illustration {
  position: absolute;
  top: 82px;
  right: 89px;
}

.commands__paragraph {
  font-size: 15px;
  line-height: 24px;
  width: 46%;
}
.commands__paragraph--about {
  margin-bottom: 22px;
}
.commands__paragraph--donate {
  margin-bottom: 50px;
}

.commands__bold {
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
}

.commands__button {
  font-weight: 500;
  font-size: 17px;
  text-decoration: none;
  border-radius: 4px;
  line-height: 25px;
  color: #ffffff;
  padding: 13px 22px;
  background: #4965ee;
  display: inline-block;
  margin-bottom: 71px;
  transition: 0.3s background ease-in;
}
.commands__button:hover {
  background: #6079f0;
}

.commands__subheading {
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  margin-bottom: 26px;
}

.commands__list {
  list-style-type: none;
}

.commands__item {
  padding: 21px 12px;
}
.commands__item:nth-child(odd) {
  background: rgba(73, 101, 238, 0.15);
  border-radius: 10px;
}

.commands__command {
  font-size: 16px;
  line-height: 24px;
}

.commands__button-show {
  padding: 13px 23px;
  background: #4965ee;
  font-weight: 500;
  font-size: 17px;
  line-height: 25px;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.3s background ease-in;
}
.commands__button-show:hover {
  background: #6079f0;
}

.commands__moderators,
.commands__rights {
  margin-bottom: 40px;
}